.line {
  position: absolute;
  bottom: 0;
  border-left: 0.125em solid gray;
  -webkit-animation: show-up 2s ease-in-out forwards;
  animation: show-up 2s ease-in-out forwards; }

@-webkit-keyframes fade-in {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes fade-in {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@-webkit-keyframes show-up {
  0% {
    height: 0; }
  50% {
    left: 0;
    height: 100%; }
  95% {
    opacity: 1; }
  100% {
    left: 100%;
    height: 100%;
    opacity: 0; } }

@keyframes show-up {
  0% {
    height: 0; }
  50% {
    left: 0;
    height: 100%; }
  95% {
    opacity: 1; }
  100% {
    left: 100%;
    height: 100%;
    opacity: 0; } }

